body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input,textarea{
width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.submit {
    width: 100%;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .submit:hover {
    background-color: #45a049;
  }
.PostForm {
    width:50%;
    margin: 0 auto;
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
    display:flex;
    justify-content: space-around;
    flex-direction: column;
  }
html{
  font-size:35px;
}
header{
  display:flex;
  flex-direction: column;
  background-color: black;
  justify-content: center;
  margin:0 auto;
}
.artifactClass{
  font-size: 0.85rem;
  color: aliceblue;
}
.CultureClass{
  font-size: 0.5rem;
  font-style: italic;
  color: aliceblue;
}
.header {
    background-color: black;
    width:80%;
    margin:0 auto;
  }
  
  /* Style the header links */
  .header a {
    float: left;
    color: white;
    text-align: center;
    padding-top: 12px;
    padding-right: 12px;
    padding-bottom: 12px;
    padding-left: 12px;
    text-decoration: none;
    font-size: 0.5rem; 
    line-height: 25px;
    border-radius: 4px;
    margin-right: 50px;
    margin-left: 50px;
    margin-bottom:2px;
  }
  
  /* Change the background color on mouse-over */
  .header a:hover {
    background-color: #ddd;
    color: black;
  }
  
  /* Style the active/current link*/
  .header a.active {
    background-color: dodgerblue;
    color: white;
  }
  
  /* Float the link section to the right */
  .header-right {
    display:flex;
    justify-content: flex-end;
  }
  @media screen and (max-width: 500px) {
    .header{
      background-color: black;
      width:100%;
    }
    .header a {
      float:none;
      display:none;
      flex-direction: row;
      background-color: black;
      width:100%;
      justify-content: center;
      margin:0 auto;
    } 
    
    .header-right a {
      display: none;
      flex-direction: row;
      justify-content: flex-end;
      margin:0;
    }
  }

footer {
    background: #000;
    color: #fff;
    position: relative;
    bottom: 0;
    width:100%;
    margin-top: 10px;
  }

  .footer p {
    margin-left:5%;
    font-size: 0.8em;
    display: flex;
    width:80%;
    margin:0 auto;  
  }
.main{
    justify-self: center;
    display: flex;
    flex-direction: column;
    border: 1px solid #787878;
    max-width: 350px;
}

.backgroundImage{

    width: 350px;
    height: 300px;
    background-size: cover;
}

.imageInfo{

    display:flex;
    justify-content: space-between;
    font-size:10px;
    color:gray;
    padding:10px;

}

.imageDesc{
    font-size: 20px;
    color:black;
    display:flex;
    flex-direction: column;
    padding:0px 10px;
    height: 100px;
}

.imageDesc span{
    margin-top:1em;;
}
.imageContent{
    font-size:0.5em;
    color:grey;
}

.clap{
    width:30px;
    height:30px;
    margin:10px
}
.claps{
    font-size: 16px;
    margin-top: 0;
    vertical-align: 18px;
}

.heart{
    float:right;
    width:30px;
    height:30px;
    margin:10px
}

.allPosts{
    margin-top: 20px;
}
PostReaction{
    bottom: 0;
}
.allPostsMain{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin:0 auto;
    width:80%

}
Post{
    align-self: stretch;
}
.button{
    background-color:blue ;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;

  
}

.createPost{
    display:flex;
    justify-content: center;
}
